.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 88;

  &-navigation {
    display: flex;
    justify-content: space-between; }

  &-home {
    img {
      width: 24px;
      height: 24px;
      object-fit: contain; } }

  &-tools {

    .nav_opener {
      display: block;
      color: $color_darkblue;

      img {
        width: 24px;
        height: 24px;
        object-fit: contain; }

      .open {
        position: relative;
        top: 7px; }
      .close {
        display: none; }

      &.active {
        .open {
          display: none; }

        .close {
          display: block; } } } }


  .menu {
    display: flex;
    align-items: center;
    background-color: $color_white;
    list-style: none;
    padding: 0;

    @include mediaMaxWidth(1060px) {
      display: none;
      width: 100%;
      bottom: 0;
      transform: translateY(100%);
      left: 0;
      position: absolute;
      z-index: 888;
      box-shadow:  0 15px 15px rgba(0,0,0,.14); }

    &-item {
      display: block;
      position: relative;
      margin: 0 26px 0 0;


      @include mediaMaxWidth(1060px) {
        width: 100%;
        text-align: center; }


      &>a {
        display: block;
        color: $color_darkblue;
        font-size: 18px;
        line-height: 88px;
        font-weight: 400;
        margin: 0;
        position: relative;

        @include mediaMaxWidth(1280px) {
          font-size: 14px; }

        @include mediaMaxWidth(1060px) {
          line-height: 50px;
          font-size: 16px; } }

      &:hover,
      &.current-menu-item {
        &>a {
          &:before {
            content: '';
            position: absolute;
            z-index: 8;
            height: 4px;
            width: 100%;
            left: 0;
            bottom: 27px;
            background-color: $color_darkblue; }

          @include mediaMaxWidth(1060px) {
            background-color: $color_blue;
            color: $color_white;

            &:before {
              display: none; } } } } } } }
